/* stylelint-disable */

@mixin big-laptop {
  @media (max-width: $big-laptop) {
    @content;
  }
}
@mixin for-laptop {
  @media (max-width: $laptop) {
    @content;
  }
}
@mixin mobile-big-landscape {
  @media (max-width: $mobile-big-landscape) {
    @content;
  }
}
@mixin mobile-landscape {
  @media (max-width: $mobile-landscape) {
    @content;
  }
}
@mixin mobile {
  @media (max-width: $mobile) {
    @content;
  }
}
@mixin small-mobile {
  @media (max-width: $small-mobile) {
    @content;
  }
}
@mixin old-mobile {
  @media (max-width: $old-mobile) {
    @content;
  }
}


